
html {
	position: relative;
	min-height: 100%;
}
body {
	/* Margin bottom by footer height */
	margin-bottom: 60px;
}
.footer {
	position: absolute;
	bottom: 0;
	text-align: center;
	width: 100%;
	height: 60px;
	line-height: 60px; 
	background-color: #eb6864;
	color: #e6e6e6
}



